<template>
  <el-container class="main-container">
    <div v-show="!TypeChange" class="moon"></div>
    <div v-show="TypeChange" class="night"></div>
    <myLight @changeBackground="changeBG" />
    <!-- 头部区域 -->
    <el-header>
      <span>Heartless</span>
      <!-- active-text-color="#409EFF" -->
      <el-menu class="el-menu-top" mode="horizontal" background-color="#414753" text-color="#fff"
        active-text-color="#f2f2f2" router>
        <el-submenu index="2" :popper-append-to-body="false">
          <template slot="title">
            <!-- 头像 -->
            <img :src="user_pic" alt="" v-if="user_pic" class="avatar" />
            <img src="../../assets/images/baby.jpg" alt="" class="avatar" v-else />
            <span>个人中心</span>
          </template>
          <el-menu-item index="/user-info"><i class="el-icon-s-operation"></i><span>基本资料</span></el-menu-item>
          <el-menu-item index="/user-avatar"><i class="el-icon-camera"></i><span>更换头像</span></el-menu-item>
          <el-menu-item index="/user-pwd"><i class="el-icon-key"></i><span>重置密码</span></el-menu-item>
          <el-menu-item @click="quitFn"><i class="el-icon-switch-button"></i><span>退出</span></el-menu-item>
        </el-submenu>
      </el-menu>
    </el-header>
    <el-container>
      <!-- 侧边栏区域 -->
      <el-aside width="230px">
        <!-- 侧边栏--个人信息 -->
        <div class="user-box">
          <!-- <img :src="user_pic" alt="" v-if="user_pic" /> -->
          <img src="../../assets/images/baby.jpg" alt="" />
          <div class="userinfo">
            <span>{{ nickname || username }}</span>
            <span>A handsome man</span>
          </div>
        </div>
        <!-- 侧边栏--侧栏导航菜单 -->
        <!--
            el-menu导航菜单
            default-active:当前激活菜单的 index
            @open:sub-menu 展开的回调
            @close:sub-menu 收起的回调

            子标签:
            如果有嵌套,先写el-submenu
            如果无嵌套,直接下el-menu-item

            unique-opened 是否只保持一个子菜单的展开
         -->
        <el-menu :default-active="$route.path" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
          background-color="rgba(0,0,0,.2)" text-color="#fff" active-text-color="#409EFF" unique-opened router>
          <el-menu-item index="/home">
            <i class="el-icon-s-home"></i>
            <span slot="title">首页</span>
          </el-menu-item>
          <el-submenu index="/item">
            <template slot="title">
              <i class="el-icon-video-camera"></i>
              <span>项目简介</span>
            </template>
            <el-menu-item index="/item/first"><i class="el-icon-s-platform"></i>PC 端</el-menu-item>
            <el-menu-item index="/item/thesecond"><i class="el-icon-mobile"></i>移动端</el-menu-item>
            <el-menu-item index="/item/third"><i class="el-icon-s-promotion"></i>小程序</el-menu-item>
          </el-submenu>
          <!-- <el-menu-item index="/cloud_album">
            <i class="el-icon-picture-outline"></i>
            <span slot="title">云相册</span>
          </el-menu-item> -->
          <el-menu-item index="/message_board">
            <i class="el-icon-picture-outline"></i>
            <span slot="title">故事集</span>
          </el-menu-item>
          <el-submenu index="/user-info">
            <template slot="title">
              <i class="el-icon-user"></i>
              <span>个人信息</span>
            </template>
            <el-menu-item index="/user-info"><i class="el-icon-s-operation"></i>基本资料</el-menu-item>
            <el-menu-item index="/user-avatar"><i class="el-icon-camera-solid"></i>更换头像</el-menu-item>
            <el-menu-item index="/user-pwd"><i class="el-icon-key"></i>重置密码</el-menu-item>
          </el-submenu>
          <el-submenu index="6">
            <template slot="title">
              <i class="el-icon-tickets"></i>
              <span>文章管理</span>
            </template>
            <el-menu-item index="/art-cate"><i class="el-icon-s-data"></i>文章分类</el-menu-item>
            <el-menu-item index="/art-list"><i class="el-icon-document-copy"></i>文章列表</el-menu-item>
          </el-submenu>
          <!-- <el-menu-item index="7">
            <i class="el-icon-setting"></i>
            <span slot="title">后台管理</span>
          </el-menu-item> -->
          <el-menu-item index="/effect">
            <i class="el-icon-loading"></i>
            <span slot="title">Github</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <!-- 页面主体区域 -->
        <el-main>
          <!-- /home路由组件 -->
          <router-view class="router"></router-view>
        </el-main>
        <!-- 底部 footer 区域 -->
        <el-footer>© A handsome man - Heartless</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
import { mapGetters } from 'vuex'
// 引入灯泡小组件
import myLight from '@/components/highlight/mylight.vue'
// 引入获取侧边栏数据的接口
// import { getMenusListAPI } from '@/api/index'
export default {
  name: 'my-layout',
  data() {
    return {
      menus: [] // 侧边栏数据
    }
  },
  components: { myLight },
  // 计算属性
  computed: {
    ...mapGetters(['username', 'nickname', 'user_pic']),
    // 判断背景图片的切换
    TypeChange() {
      return this.$store.state.fontColor
    }
  },
  methods: {
    // 1.退出登录的确认弹窗
    quitFn() {
      this.$confirm('是否退出登录?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        closeOnClickModal: false,
        // 自定义类名
        customClass: 'winClass'
      })
        .then(() => {
          // 确定操作
          this.$message({
            type: 'success',
            message: '退出成功！',
            customClass: 'windthClass',
            duration: 2000
          })
          // 清除vuex的token
          this.$store.commit('updateToken', '')
          // 清除vuex的userInfo(用户信息对象)
          this.$store.commit('updateUserInfo', {})
          // 跳转到登录页面
          this.$router.push('/login')
        })
        .catch(() => {
          // 取消操作
          this.$message({
            type: 'warning',
            message: '已取消！',
            customClass: 'windthClass',
            duration: 2000
          })
        })
    },
    // 2.侧边导航栏的菜单方法
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    // 3.小灯泡组件点击事件
    changeBG() {
      this.$store.commit('updatefont', !this.$store.state.fontColor)
    }
    // 4.获取侧边栏的数据函数
    // async getMenuListAPI() {
    //   const { data: res } = await getMenusListAPI()
    //   console.log(res)
    // }
  },
  created() {
    // 5.调用侧边栏的处理函数(去掉了,没有必要从后台获取)
    // this.getMenuListAPI()
  }
}
</script>

<style lang="less" scoped>
.main-container {
  width: 1380px;
  height: 100%;
  margin: auto;

  .el-header {
    background-color: #414753;
    z-index: 999;
  }

  .moon,
  .night {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: -1;
  }

  .night {
    background: url('../../assets/images/night.gif') center;
  }

  .moon {
    background: url('../../assets/images/meinv.jpg') no-repeat;
    background-size: 100% 100%;
  }

  .el-aside {
    background-color: rgba(43, 44, 40, 0.6);
    overflow: hidden;

    // height: 900px;
    .user-box {
      height: 100px;
      display: flex;
      // 主轴上子元素排列
      justify-content: center;
      // 侧轴上子元素排列-单行居中
      align-items: center;
      border-top: 1px solid #dae0de;
      border-bottom: 2px solid #dae0de;
      // 页面文字不能被选中
      user-select: none;

      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: #fff;
        margin-right: 15px;
        // 高度铺满，宽度等比缩放，超出则裁剪
        object-fit: cover;
        box-shadow: 0 0 5px #ccc;
        overflow: hidden;
      }

      .userinfo {
        // flex布局
        display: flex;
        // 设置主轴方向
        flex-direction: column;

        span:first-of-type {
          color: lightblue;
          font-weight: 700;
          font-size: 14px;
          margin-bottom: 2px;
        }

        span:last-of-type {
          color: #a0a0a0;
          margin-top: 2px;
          font-size: 12px;
        }
      }
    }
  }

  .el-header {
    padding: 0;
    display: flex;
    justify-content: space-between;

    >span {
      font-size: 20px;
      font-weight: 700;
      line-height: 60px;
      color: #d4d4d4;
      margin-left: 50px;
    }

    .el-submenu .el-menu-item {
      min-width: 100px;
    }

    .el-submenu>.el-submenu__title>span {
      font-weight: 550;
    }
  }

  .el-main {
    // overflow-y: scroll;
    // height: 0;
    background-color: rgba(242, 242, 242, 0.6);
    padding: 5px;
  }

  .el-footer {
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    background-color: rgba(242, 242, 242, 0.6);
  }
}

.avatar {
  border-radius: 50%;
  width: 35px;
  height: 35px;
  background-color: #fff;
  margin-right: 10px;
  object-fit: cover;
}

/deep/ .el-menu--popup {
  min-width: 162px;

  .el-menu-item>span {
    margin-left: 10px;
  }
}

/deep/ .el-menu--horizontal>.el-submenu.is-active .el-submenu__title {
  border-bottom: 0px;
  font-weight: 700;
}

/deep/ .el-menu {
  border-right: 0px;
}

/deep/ .el-submenu__title:hover {
  background-color: rgba(43, 44, 40, 0.6) !important;
}

/deep/ .el-submenu .el-menu-item {
  background-color: rgba(0, 0, 0, 0.1) !important;
}

/deep/ .el-menu-item:focus,
/deep/ .el-menu-item:hover {
  background-color: rgba(43, 44, 40, 0.6) !important;
}

.router {
  width: 100%;
  padding: 5px;
  animation: animate 2s;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}</style>
